import Web3 from 'web3'
import {address, ABI} from './constants/dicegameContract'

let getContract = new Promise(function (resolve, reject) {
  let web3 = new Web3(window.web3.currentProvider)
  let dicegameContract = web3.eth.contract(ABI)
  let dicegameContractInstance = dicegameContract.at(address)
  // dicegameContractInstance = () => dicegameContractInstance
  resolve(dicegameContractInstance)
})

export default getContract



