               <p>The result is as follows:</p>
               <p>dice1: {{ d1 }}, dice2: {{ d2 }}, sum: {{ sum }}</p>
               <p v-if="mside == 1 ">Maker: 半(ODD) You:丁(EVEN)</p>
               <p v-if="mside == 2 ">Maker: 丁(EVEN) You:半(ODD)</p>
             </section>
