<style>
.hero {
  background:#ddd url('../assets/hero-img.jpg') center center;
  background-size:cover;
}
.hero-body {
  padding-top: 5px;
}
.navbar-menu{
  background-color:transparent !important;
}
.fa-dice{
  margin-right:5px;
}
</style>
<script>
import firebase from 'firebase/app'
import 'firebase/firestore'
export default {
  name: 'Make',
  data () {
    return {
      picked: null,
      Makerlist: {},
      MakeCompleteModal: false,
      MakeMissMyselfModal: false,
      MakeMissParityModal: false,
      TakeCompleteModal: false,
      TakeMissTimingModal: false,
      TakeMissMyselfModal: false,
      TakeActive: true,
      Terms0fUseModal: true,
      d1: false,
      d2: false,
      sum: false,
      chohan: false,
      isFullPage: true,
      mside: false,
      game: 'Which',
      winEvent: null
    }
  },
  mounted () {
    this.$store.dispatch('getContractInstance')
    const firestore = firebase.firestore()
    const settings = {timestampsInSnapshots: true}
    firestore.settings(settings)
    var self = this
    firestore.collection('Incomplete').onSnapshot(function (querySnapshot) {
      self.Makerlist = {}
      querySnapshot.forEach(function (doc) {
        self.Makerlist[doc.id] = doc.data().side
      })
    })
    var burger = document.querySelector('.burger')
    var menu = document.querySelector('#' + burger.dataset.target)
    burger.addEventListener('click', function () {
      burger.classList.toggle('is-active')
      menu.classList.toggle('is-active')
    })
  },
  computed: {
    web3 () {
      return this.$store.state.web3
    }
  },
  methods: {
    make: function () {
      var self = this
      const firestore = firebase.firestore()
      const settings = {timestampsInSnapshots: true}
      firestore.settings(settings)
      if (this.picked != null) {
        firestore.collection('Incomplete').doc(self.$store.state.web3.coinbase).get().then(function (doc) {
          if (!doc.exists) {
            const loadingComponent = self.$loading.open({
              container: self.isFullPage ? null : self.$refs.element.$el
            })
            Promise.resolve()
              .then(function () {
                return new Promise((resolve, reject) => {
                  self.$store.state.contractInstance().make(self.picked, {
                    gas: 300000,
                    value: self.$store.state.web3.web3Instance().toWei(0.02, 'ether'),
                    from: self.$store.state.web3.coinbase
                  }, (err, result) => {
                    if (err) {
                      console.log(err)
                    } else {
                      resolve()
                    }
                  })
                })
              })
              .then(function () {
                return new Promise((resolve, reject) => {
                  let Complete = self.$store.state.contractInstance().Makecomplete()
                  Complete.watch((err, result) => {
                    if (err) {
                      console.log('could not get event Makecomplete()')
                    } else {
                      loadingComponent.close()
                      self.MakeCompleteModal = result.args._status
                      resolve()
                    }
                  })
                })
              })
              .then(function () {
                return new Promise((resolve, reject) => {
                  firestore.collection('Incomplete').doc(self.$store.state.web3.coinbase).set({
                    address: self.$store.state.web3.coinbase,
                    side: self.picked
                  })
                  resolve()
                })
              })
          } else {
            self.MakeMissMyselfModal = true
          }
        })
      } else {
        self.MakeMissParityModal = true
      }
    },
    take: function (makeraddress, side) {
      this.TakeActive = false
      this.winEvent = null
      var self = this
      Promise.resolve()
        .then(function () {
          return new Promise((resolve, reject) => {
            self.TakeActive = false
            if (makeraddress === self.$store.state.web3.coinbase) {
              self.TakeMissMyselfModal = true
            } else {
              const firestore = firebase.firestore()
              const settings = {timestampsInSnapshots: true}
              firestore.settings(settings)
              firestore.collection('Incomplete').doc(makeraddress).get().then(function (doc) {
                if (doc.exists) {
                  self.$store.state.contractInstance().take(makeraddress, {
                    gas: 300000,
                    value: self.$store.state.web3.web3Instance().toWei(0.02, 'ether'),
                    from: self.$store.state.web3.coinbase
                  }, (err, result) => {
                    if (err) {
                      console.log(err)
                      self.TakeActive = true
                    } else {
                      const loadingComponent = self.$loading.open({
                        container: self.isFullPage ? null : self.$refs.element.$el
                      })
                      const firestore = firebase.firestore()
                      const settings = {timestampsInSnapshots: true}
                      firestore.settings(settings)
                      firestore.collection('Incomplete').doc(makeraddress).delete().then(function () {
                        console.log('remove from betslist')
                      }).catch(function (error) {
                        console.error('Error removing document: ', error)
                      })
                      Promise.resolve()
                        .then(function () {
                          return new Promise((resolve, reject) => {
                            let Won = self.$store.state.contractInstance().Won()
                            Won.watch((err, result) => {
                              if (err) {
                                console.log('could not get event Won()')
                              } else {
                                self.winEvent = result.args
                                self.d1 = self.winEvent._d1.c[0]
                                self.d2 = self.winEvent._d2.c[0]
                                self.sum = self.d1 + self.d2
                                self.chohan = self.winEvent._chohan.c[0].toString()
                                resolve()
                              }
                            })
                          })
                        })
                        .then(function () {
                          return new Promise((resolve, reject) => {
                            self.mside = side
                            if (self.chohan === side) {
                              self.game = 'BETTER LUCK NEXT TIME!'
                            } else {
                              self.game = 'WINNER WINNER CHICKEN DINNER!'
                            }
                            resolve()
                          })
                        })
                        .then(function () {
                          loadingComponent.close()
                          self.TakeCompleteModal = true
                          resolve()
                        })
                    }
                  })
                } else {
                  self.TakeMissTimingModal = true
                  self.TakeActive = true
                }
              })
            }
          })
        })
        .then(function () {
          return new Promise((resolve, reject) => {
            self.TakeActive = true
            resolve()
          })
        })
    }
  }
}
</script>

